// calc waypoints traveling along vertices
function calcWaypoints(vertices) {
    var waypoints = [];
    for (var i = 1; i < vertices.length; i++) {
        var pt0 = vertices[i - 1];
        var pt1 = vertices[i];
        var dx = pt1.x - pt0.x;
        var dy = pt1.y - pt0.y;
        for (var j = 0; j < 25; j++) {
            var x = pt0.x + (dx * j) / 25;
            var y = pt0.y + (dy * j) / 25;
            waypoints.push({ x: x, y: y });
        }
    }
    return waypoints;
}
