const POST = 'post';
const GET = 'get';

// DEV API ENDPOINT: https://dev.chancedot.io
// PRODUCTION API ENDPOINT: https://api.chancedot.io/prd/api
const API_ENDPOINT = 'https://api.chancedot.io/prd/api';

const fetches = (host => {
    return (URL, fetchOpts) =>
        fetch(`${host}${URL}`, fetchOpts).then(response => response.json());
})(API_ENDPOINT);

export function postEmailReservation({ email, recaptchaValue }) {
    return fetches(`/reservation`, {
        method: POST,
        headers: {
            Accept: 'application/json',
            'X-Requested-With': 'XMLHttpRequest'
        },
        body: JSON.stringify({
            email,
            'g-recaptcha-response': recaptchaValue
        })
    });
}

export function postEmailReservationVerify({ email, reserveCity, verifyCode }) {
    return fetches(`/reservation/verify`, {
        method: POST,
        headers: {
            Accept: 'application/json',
            'X-Requested-With': 'XMLHttpRequest'
        },
        body: JSON.stringify({
            email,
            reserveCity,
            verifyCode
        })
    });
}

export function getReservationPopulationCount() {
    return fetches(`/reservation/counter/info`, {
        method: GET
    });
}
