import React, { useState, useRef, useEffect } from 'react';
/** @jsx jsx */
import { jsx, css } from '@emotion/react';
import Typography from '@material-ui/core/Typography';
import ScrollTrigger from 'react-scroll-trigger';
import AnimatedNumber from 'animated-number-react';

import { images } from '../constants';
import canvasCountryPosition from '../configs/canvasCountryPosition';

const CANVAS_WIDTH = 2560;
const CANVAS_HEIGHT = 2560;
const STROKE_COLOR = '#7f8ca2';

// let isDrawing = false;

function PopulationMap({ reserveCount, populationList }) {
    const canvasMapRef = useRef(null);
    const canvasTextRef = useRef(null);
    const northImgElementRef = useRef(null);
    const northCentralImgElementRef = useRef(null);
    const northCentralSouthImgElementRef = useRef(null);

    // const [isInitDone, setIsInitDone] = useState(false);
    const [isImageLoaded, setIsImageLoaded] = useState(false);
    const [isEnter, setIsEnter] = useState(false);
    const [isDuringAnimation, setIsDuringAnimation] = useState(false);

    // initial
    useEffect(async () => {
        const canvasMapElement = canvasMapRef.current;

        await initImgElement(
            northImgElementRef,
            northCentralImgElementRef,
            northCentralSouthImgElementRef
        );
        await initMap({
            canvasMapElement,
            imgElement: northImgElementRef.current,
            width: CANVAS_WIDTH,
            height: CANVAS_HEIGHT
        });

        setIsImageLoaded(true);
    }, []);

    useEffect(async () => {
        const canvasMapElement = canvasMapRef.current;
        const canvasTextElement = canvasTextRef.current;

        if (
            populationList !== null &&
            isImageLoaded &&
            isEnter &&
            !isDuringAnimation
        ) {
            setIsDuringAnimation(true);

            clearCanvas(canvasMapElement);
            clearCanvas(canvasTextElement);

            await initMap({
                canvasMapElement,
                imgElement: northImgElementRef.current,
                width: CANVAS_WIDTH,
                height: CANVAS_HEIGHT
            });
            await populationAnimation(
                canvasTextElement,
                canvasMapElement,
                northCentralImgElementRef,
                northCentralSouthImgElementRef,
                populationList
            );

            setIsDuringAnimation(false);
        }
    }, [populationList, isImageLoaded, isEnter]);

    // useEffect(async () => {
    //     const canvasMapElement = canvasMapRef.current;
    //     const canvasTextElement = canvasTextRef.current;

    //     if (populationList !== null && isImageLoaded && isEnter) {
    //         if (isInitDone) {
    //             await repaintAllCanvasWithoutAnimation(
    //                 canvasTextElement,
    //                 canvasMapElement,
    //                 northCentralSouthImgElementRef,
    //                 populationList
    //             );
    //         } else {
    //             if (!isDrawing) {
    //                 isDrawing = true;

    //                 await populationAnimation(
    //                     canvasTextElement,
    //                     canvasMapElement,
    //                     northCentralImgElementRef,
    //                     northCentralSouthImgElementRef,
    //                     populationList
    //                 );

    //                 setIsInitDone(true);
    //             }
    //         }
    //     }
    // }, [populationList, isImageLoaded, isEnter]);

    return (
        <ScrollTrigger onEnter={() => setIsEnter(true)}>
            <section id="populationMap" css={rootContainerStyle}>
                <Typography
                    css={css`
                        color: ${STROKE_COLOR};
                        text-align: center;
                        &.MuiTypography-root {
                            font-size: 48px;
                        }
                        @media (max-width: 768px) {
                            &.MuiTypography-root {
                                font-size: 36px;
                            }
                        }
                    `}
                >
                    :{' '}
                    <AnimatedNumber
                        value={reserveCount}
                        formatValue={value => ~~value}
                    />
                </Typography>
                <Typography
                    variant="caption"
                    css={css`
                        color: #666666;
                        margin-bottom: 32px;
                    `}
                >
                    
                </Typography>
                <div css={canvasContainerStyle}>
                    <canvas
                        css={canvasMapLayerStyle}
                        ref={canvasMapRef}
                        width={CANVAS_WIDTH}
                        height={CANVAS_HEIGHT}
                    />
                    <canvas
                        css={canvasTextLayerStyle}
                        ref={canvasTextRef}
                        width={CANVAS_WIDTH}
                        height={CANVAS_HEIGHT}
                    />
                </div>
            </section>
        </ScrollTrigger>
    );
}

export default React.memo(PopulationMap);

async function initImgElement(nRef, ncRef, ncsRef) {
    nRef.current = await onImageLoad(images.taiwanNorth);
    ncRef.current = await onImageLoad(images.taiwanNorthCentral);
    ncsRef.current = await onImageLoad(images.taiwanNorthCentralSouth);
}

async function initMap({ canvasMapElement, imgElement, width, height } = {}) {
    const ctx = canvasMapElement.getContext('2d');
    const ratio =
        imgElement.naturalWidth > imgElement.naturalHeight
            ? width / imgElement.naturalWidth
            : height / imgElement.naturalHeight;

    clearCanvas(canvasMapElement);
    ctx.drawImage(
        imgElement,
        0,
        0,
        imgElement.naturalWidth,
        imgElement.naturalHeight,
        width / 2 - (imgElement.naturalWidth * ratio) / 2,
        height / 2 - (imgElement.naturalHeight * ratio) / 2,
        imgElement.naturalWidth * ratio,
        imgElement.naturalHeight * ratio
    );
}

async function populationAnimation(
    canvasTextElement,
    canvasMapElement,
    ncImgRef,
    ncsImgRef,
    populationList
) {
    const ctx = canvasTextElement.getContext('2d');
    const countryList = Object.keys(canvasCountryPosition);
    const typeMapping = {
        northCentral: ncImgRef.current,
        northCentralSouth: ncsImgRef.current
    };

    let promiseArray = [];
    let currentType = 'north';
    let index = 0;

    ctx.strokeStyle = STROKE_COLOR;
    ctx.lineWidth = 8;

    for (const country of countryList) {
        index++;

        const { waypoints, type, name, textAlign } = canvasCountryPosition[
            country
        ](canvasTextElement.width, canvasTextElement.height);
        const count = populationList[name] || 0;
        const text = `${name} ${count} `;

        if (currentType === type) {
            promiseArray.push(drawLineAnimation(ctx, waypoints));
            promiseArray.push(
                fadeInTextAnimation(
                    ctx,
                    text,
                    textAlign,
                    waypoints[waypoints.length - 1].x,
                    waypoints[waypoints.length - 1].y
                )
            );

            if (index === countryList.length - 1) {
                await Promise.all(promiseArray);
            }
        } else {
            await Promise.all(promiseArray);

            initMap({
                canvasMapElement,
                imgElement: typeMapping[type],
                width: canvasTextElement.width,
                height: canvasTextElement.height
            });
            promiseArray = [];
            promiseArray.push(drawLineAnimation(ctx, waypoints));
            promiseArray.push(
                fadeInTextAnimation(
                    ctx,
                    text,
                    textAlign,
                    waypoints[waypoints.length - 1].x,
                    waypoints[waypoints.length - 1].y
                )
            );
            currentType = type;
        }
    }
}

