function fadeInTextAnimation(ctx, text, textAlign, x, y) {
    return new Promise(resolve => {
        let alpha = 0; /// current alpha value
        let delta = 0.01; /// delta = speed

        function repaint() {
            const fixedX = textAlign === 'start' ? x + 6 : x - 6;
            const fixedY = y + 24;
            alpha += delta;

            ctx.font = 'bold 72px verdana, sans-serif ';
            ctx.fillStyle = `rgba(127, 140, 162, ${alpha})`;
            ctx.textAlign = textAlign;
            ctx.fillText(text, fixedX, fixedY);
            alpha += delta;

            if (alpha > 1) {
                resolve(true);
            } else {
                requestAnimationFrame(repaint);
            }
        }

        repaint();
    });
}

function onImageLoad(imgSrc) {
    return new Promise(resolve => {
        const imgElement = document.createElement('img');

        imgElement.src = imgSrc;
        imgElement.onload = () => resolve(imgElement);
    });
}

// incrementally draw additional line segments along the path
function drawLineAnimation(ctx, waypoints) {
    return new Promise(reslove => {
        let t = 1;

        function repaint() {
            if (t < waypoints.length - 1) {
                requestAnimationFrame(repaint);
            }
            // draw a line segment from the last waypoint
            // to the current waypoint
            ctx.beginPath();
            ctx.moveTo(waypoints[t - 1].x, waypoints[t - 1].y);
            ctx.lineTo(waypoints[t].x, waypoints[t].y);
            ctx.stroke();
            // increment "t" to get the next waypoint
            t++;

            if (t === waypoints.length) {
                reslove(true);
            }
        }

        repaint();
    });
}

function clearCanvas(canvasElement) {
    const ctx = canvasElement.getContext('2d');

    ctx.clearRect(0, 0, canvasElement.width, canvasElement.height);
}

const rootContainerStyle = css`
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    padding: 32px 0;
    background-image: linear-gradient(18deg, #eaae9b -248%, #b5c3d0);
`;

const canvasContainerStyle = css`
    position: relative;
`;

const canvasMapLayerStyle = css`
    width: 100%;
    // max-width: 1080px;
`;

const canvasTextLayerStyle = css`
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
`;
