export default React.memo(AppIntroduction);

const rootContainerStyle = css`
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background-image: linear-gradient(-119deg, #eaae9b, #b4c3d0);
    text-align: center;
    overflow: hidden;

    @media (max-width: 768px) {
        height: 100%;
        padding: 48px;
    }
`;

const pcFontStyle = css`
    @media (max-width: 768px) {
        display: none;
    }
`;

const mobileFontStyle = css`
    display: none;
    @media (max-width: 768px) {
        display: block;
    }
`;

const maxWidthContainerStyle = css`
    max-width: 760px;
    transition: 0.8s;
    transform: translateX(10px);
    opacity: 0;
`;

const showingStyle = css`
    opacity: 1;
    transform: translateX(0px);
`;

const fontStyle = css`
    color: #ffffff;
`;

const imagesWithDescContainerStyle = css`
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    margin-top: 16px;

    @media (max-width: 768px) {
        display: none;
    }
`;

const sliderStyle = css`
    display: none;
    @media (max-width: 768px) {
        display: block;
    }
`;

const blockStyle = css`
    flex-basis: 30%;
`;

const imageStyle = css`
    margin-bottom: 8px;
    width: 100%;
    @media (max-width: 768px) {
        max-width: 260px;
        margin: auto auto 24px;
    }
`;

const descTextStyle = css`
    color: #ffffff;
`;
