import React, { useState, useEffect, useMemo } from 'react';
/** @jsx jsx */
import { jsx, css } from '@emotion/react';

import Header from '../components/Header';
import Reserve from '../components/Reserve';
import AppDiffFeatures from '../components/AppDiffFeatures';
import PopulationMap from '../components/PopulationMap';
import AppIntroduction from '../components/AppIntroduction';
import Market from '../components/Market';
import Prize from '../components/Prize';
import Footer from '../components/Footer';
import InitialDialog from '../components/InitialDialog';
import Menu from '../components/Menu';
import SEO from '../components/SEO';
import { getReservationPopulationCount } from '../api';

export default function Home() {
    const [populationList, setPopulationList] = useState(null);
    const reserveCount = useMemo(() => {
        if (populationList === null) return 0;

        return Object.entries(populationList).reduce(
            (acc, cur) => acc + cur[1],
            0
        );
    }, [populationList]);

    useEffect(async () => {
        try {
            const response = await getReservationPopulationCount();
            setPopulationList(response.reservationCounterInfo || {});
        } catch (error) {
            console.log(error);
        }

        setInterval(async () => {
            try {
                const response = await getReservationPopulationCount();
                setPopulationList(response.reservationCounterInfo || {});
            } catch (error) {
                console.log(error);
            }
        }, 15000);
    }, []);

    return (
        <>
            <SEO />
            <InitialDialog />
            <Header />
            <Menu />
            <main css={rootContainerStyle}>
                <Reserve />
                <AppDiffFeatures />
                <Market />
                <PopulationMap
                    reserveCount={reserveCount}
                    populationList={populationList}
                />
                <AppIntroduction />
                <Prize reserveCount={reserveCount} />
            </main>
            <Footer />
        </>
    );
}

const rootContainerStyle = css`
    margin-top: 42px;
`;
