import React, { useState, useRef, useEffect } from 'react';
/** @jsx jsx */
import { jsx, css } from '@emotion/react';
import Recaptcha from 'react-google-recaptcha';
import { makeStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import TextField from '@material-ui/core/TextField';
import FormControl from '@material-ui/core/FormControl';
import InputLabel from '@material-ui/core/InputLabel';
import Select from '@material-ui/core/Select';
import MenuItem from '@material-ui/core/MenuItem';
import ScrollTrigger from 'react-scroll-trigger';
import CircularProgress from '@material-ui/core/CircularProgress';
import { isMobile } from 'react-device-detect';

import { images } from '../constants';
import { RESERVE_COUNTRY_LIST } from '../configs/reserveCountryList';
import { postEmailReservation, postEmailReservationVerify } from '../api';

const SITE_KEY = '6LfLTeYaAAAAAE-hywkWniFSzw6SY52htyOrH3p1';

const useStyles = makeStyles(() => ({
    textField: {
        margin: '8px 0'
    },
    formControl: {
        margin: '8px 0'
    }
}));

let timer = null;

function Reserve() {
    const classes = useStyles();
    const [recaptchaValue, setRecaptchaValue] = useState('');
    const [selectedCountry, setSelectedCountry] = useState('');
    const [emailTextFieldValue, setEmailTextFieldValue] = useState('');
    const [verifyCodeTextFieldValue, setVerifyCodeTextFieldValue] = useState(
        ''
    );
    const [isSendEmail, setIsSendEmail] = useState(false);
    const [isSending, setIsSending] = useState(false);
    const [isEnter, setIsEnter] = useState(false);
    const [verifyCodeLiveTime, setVerifyCodeLiveTime] = useState(5);
    const recaptchaRef = useRef(null);

    const startTimer = () => {
        timer = setInterval(() => {
            setVerifyCodeLiveTime(prev => prev - 1);
        }, 1000);
    };

    const onSelectCountryChangeHandler = event => {
        setSelectedCountry(event.target.value);
    };

    const onReacptchaChangeHandler = value => {
        setRecaptchaValue(value);
    };

    function validateEmail(email) {
        const re = /^(([^<>()[\]\\.,;:\s@\"]+(\.[^<>()[\]\\.,;:\s@\"]+)*)|(\".+\"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;

        return re.test(email);
    }

    const sendEmailHandler = async () => {
        if (!validateEmail(emailTextFieldValue)) {
            alert('Email ');

            return;
        }

        setIsSending(true);

        try {
            const response = await postEmailReservation({
                email: emailTextFieldValue,
                recaptchaValue: recaptchaValue
            });
            const { statusCode } = response;

            if (statusCode === 200) {
                setIsSendEmail(true);
                setVerifyCodeLiveTime(response.timeToLive);
                startTimer();
            } else if (statusCode === 409) {
                alert(' Email ');
            } else if (statusCode === 422) {
                alert('Email ');
            } else if (statusCode === 466) {
                alert(' Email ');
            } else if (statusCode === 467) {
                setIsSendEmail(true);
                setVerifyCodeLiveTime(response.timeToLive);
                startTimer();
            } else if (statusCode === 502) {
                alert(' Email ');
            } else {
                alert('');
            }

            recaptchaRef.current.reset();
            setIsSending(false);
        } catch (error) {
            console.log(error);
            alert('');
            recaptchaRef.current.reset();
            setIsSending(false);
        }
    };

    const sendVerifyCodeHandler = async () => {
        if (!RESERVE_COUNTRY_LIST[selectedCountry]) {
            alert('');

            return;
        }
        if (verifyCodeTextFieldValue === '') {
            alert('');

            return;
        }

        setIsSending(true);

        try {
            const response = await postEmailReservationVerify({
                email: emailTextFieldValue,
                reserveCity: RESERVE_COUNTRY_LIST[selectedCountry],
                verifyCode: verifyCodeTextFieldValue
            });
            const { lastVerifyTimes, statusCode } = response;

            if (statusCode === 200) {
                alert('');
                resetSetting();
            } else if (statusCode === 401) {
                resetSetting();
                alert('');
            } else if (statusCode === 409) {
                alert(' Email ');
                resetSetting();
            } else if (statusCode === 422) {
                alert('Email ');
            } else if (statusCode === 461) {
                alert('');
                resetSetting();
            } else if (statusCode === 403) {
                alert(
                    ` ${lastVerifyTimes} `
                );
            } else if (statusCode === 502) {
                alert(' Email ');
            } else {
                alert('');
            }

            setIsSending(false);
        } catch (error) {
            console.log(error);
            alert('');
            setIsSending(false);
        }
    };

    function resetSetting() {
        setIsSending(false);
        setIsSendEmail(false);
        setSelectedCountry('');
        setVerifyCodeTextFieldValue('');
        setEmailTextFieldValue('');
        clearInterval(timer);
    }

    useEffect(() => {
        if (verifyCodeLiveTime === 0) {
            resetSetting();
        }
    }, [verifyCodeLiveTime]);

    return (
        <ScrollTrigger
            onEnter={() => {
                setIsEnter(true);
            }}
        >
            <section id="reservation" css={rootContainerStyle}>
                <div css={[flexColumnContainerStyle, isEnter && showingStyle]}>
                    <Typography css={fontWhiteColorStyle} variant="h6">
                        
                    </Typography>
                    <img
                        css={coverPageIntroImageStyle}
                        src={images.coverPageIntro}
                        alt="chance"
                    />
                    <Typography css={fontWhiteColorStyle} variant="h6">
                        
                    </Typography>
                    <Typography css={fontWhiteColorStyle} variant="h6">
                        
                    </Typography>
                </div>
                <div
                    css={[
                        flexColumnContainerStyle,
                        fontGrayColorStyle,
                        isEnter && showingStyle
                    ]}
                >
                    <Typography variant="subtitle1">
                        chance
                    </Typography>
                    <Typography variant="subtitle1">
                        
                    </Typography>
                    <Typography variant="subtitle1">
                        
                        <br />
                        
                    </Typography>
                    <Typography css={fontRedColorStyle} variant="subtitle1">
                        DysonSwitchAirpods Pro 
                    </Typography>
                    <Typography
                        variant="body1"
                        css={[
                            roundTextWrapperStyle,
                            css`
                                margin-top: 32px;
                            `
                        ]}
                    >
                        {Boolean(isMobile) && <br />}
                        2021523-
                    </Typography>
                    <div css={getFormContainerStyle(isSendEmail)}>
                        <div
                            css={css`
                                position: absolute;
                                width: 90%;
                                top: 50%;
                                left: ${isSendEmail ? '-50' : '50'}%;
                                transform: translate(-50%, -50%);
                                transition: 0.6s;
                            `}
                        >
                            <TextField
                                css={textFieldStyle}
                                value={emailTextFieldValue}
                                className={classes.textField}
                                disabled={isSendEmail}
                                label=""
                                variant="outlined"
                                onChange={e =>
                                    setEmailTextFieldValue(e.target.value)
                                }
                            />
                            <Recaptcha
                                ref={recaptchaRef}
                                sitekey={SITE_KEY}
                                onChange={onReacptchaChangeHandler}
                            />
                        </div>
                        <div
                            css={css`
                                position: absolute;
                                width: 90%;
                                top: 50%;
                                left: ${isSendEmail ? '50' : '150'}%;
                                transform: translate(-50%, -50%);
                                transition: 0.6s;
                            `}
                        >
                            <FormControl
                                css={formControlStyle}
                                className={classes.formControl}
                                variant="filled"
                            >
                                <InputLabel id="countryList"></InputLabel>
                                <Select
                                    labelId="countryList"
                                    css={selectStyle}
                                    value={selectedCountry}
                                    onChange={onSelectCountryChangeHandler}
                                >
                                    {RESERVE_COUNTRY_LIST.map(
                                        (country, index) => (
                                            <MenuItem value={index} key={index}>
                                                {country}
                                            </MenuItem>
                                        )
                                    )}
                                </Select>
                            </FormControl>
                            <TextField
                                css={textFieldStyle}
                                value={verifyCodeTextFieldValue}
                                className={classes.textField}
                                label=""
                                variant="outlined"
                                onChange={e =>
                                    setVerifyCodeTextFieldValue(e.target.value)
                                }
                            />
                            <Typography variant="body2">
                                 Email:{' '}
                                {emailTextFieldValue}
                            </Typography>
                            <Typography variant="body2">
                                : {verifyCodeLiveTime} 
                            </Typography>
                        </div>
                    </div>
                    <Typography
                        variant="body1"
                        css={[
                            roundTextWrapperStyle,
                            css`
                                cursor: ${isSending
                                    ? 'not-allowed'
                                    : 'pointer'};
                                position: relative;
                                color: ${isSending
                                    ? 'rgba(255, 255, 255, 0.6)'
                                    : '#ffffff'};
                                background-color: ${isSending
                                    ? 'rgba(0, 0, 0, 0.2)'
                                    : '#7f8ca2'};
                            `
                        ]}
                        onClick={
                            isSending
                                ? () => {}
                                : isSendEmail
                                ? sendVerifyCodeHandler
                                : sendEmailHandler
                        }
                    >
                        {isSendEmail
                            ? ''
                            : ''}
                        {Boolean(isSending) && (
                            <CircularProgress
                                css={css`
                                    position: absolute;
                                    top: 0%;
                                    left: 0%;
                                    right: 0%;
                                    bottom: 0%;
                                    margin: auto;
                                    width: 25px !important;
                                    height: 25px !important;
                                `}
                                color="primary"
                            />
                        )}
                    </Typography>
                    <Typography css={fontGrayColorStyle} variant="subtitle1">
                        
                    </Typography>
                    <div css={downloadContainerStyle}>
                        <img
                            css={downloadImageStyle}
                            src={images.downloadAppStore}
                            alt="chance app store "
                        />
                        <img
                            css={downloadImageStyle}
                            src={images.downloadGooglePlay}
                            alt="chance google paly "
                        />
                    </div>
                </div>
            </section>
        </ScrollTrigger>
    );
}

export default React.memo(Reserve);

const showingStyle = css`
    opacity: 1;
    transform: translateX(0px);
`;

const rootContainerStyle = css`
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    padding: 24px;
    height: 100vh;
    background-image: linear-gradient(-119deg, #eaae9b, #b4c3d0);

    @media (max-width: 768px) {
        height: 100%;
        flex-direction: column;
    }
`;

const flexColumnContainerStyle = css`
    display: flex;
    flex-direction: column;
    justify-content: canter;
    align-items: center;
    width: 100%;
    max-width: 450px;
    text-align: center;
    opacity: 0;
    transform: translateX(-10px);
    transition: 0.8s;

    @media (max-width: 768px) {
        margin-bottom: 48px;
    }
`;

const coverPageIntroImageStyle = css`
    width: 100%;
    max-width: 300px;
    margin: 32px 0;
`;

const fontWhiteColorStyle = css`
    color: #ffffff;
`;

const fontGrayColorStyle = css`
    color: #484848;
`;

const fontRedColorStyle = css`
    color: #ff1c1c;
`;

const getFormContainerStyle = isSendEmail => css`
    display: flex;
    align-items: center;
    padding: 0 16px;
    width: 90%;
    position: relative;
    min-height: ${isSendEmail ? '205' : '185'}px;
    overflow: hidden;
    transition: 0.4s;
`;

const roundTextWrapperStyle = css`
    border-radius: 999px;
    background-color: #7f8ca2;
    width: 100%;
    padding: 8px 0;
    color: #ffffff;
    text-align: center;
    margin: 8px 0;
`;

const textFieldStyle = css`
    color: #5d5d5c;
    font-size: 20px;
    width: 100%;
    background-color: transparent;
    border-radius: 6px;
    text-align: center;
`;

const selectStyle = css`
    color: #5d5d5c;
    font-size: 20px;
    width: 100%;
    background-color: transparent;
    border-radius: 6px;
    text-align: center;
    border-bottom: none;
`;

const formControlStyle = css`
    width: 100%;
`;

const downloadContainerStyle = css`
    width: 100%;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    margin-top: 8px;
`;

const downloadImageStyle = css`
    width: 100%;
    max-width: 150px;
`;
