import React, { useState } from 'react';
/** @jsx jsx */
import { jsx, css, keyframes } from '@emotion/react';
import Typography from '@material-ui/core/Typography';
import Dialog from '@material-ui/core/Dialog';
import ScrollTrigger from 'react-scroll-trigger';
import AppBar from '@material-ui/core/AppBar';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';

import { images } from '../constants';

function Market() {
    const [open, setOpen] = useState(false);
    const [tabValue, setTabValue] = useState(0);
    const [isEnter, setIsEnter] = React.useState(false);

    const handleClickOpen = () => {
        setOpen(true);
    };

    const handleClose = () => {
        setOpen(false);
    };

    const handleTabsChange = (event, newValue) => {
        setTabValue(newValue);
    };

    return (
        <ScrollTrigger onEnter={() => setIsEnter(true)}>
            <section id="appFeatures" css={rootContainerStyle}>
                <Dialog open={open} onClose={handleClose}>
                    <div
                        css={css`
                            max-width: 320px;
                            & .MuiTabs-flexContainer {
                                justify-content: space-evenly;
                                color: #000000;
                                background-color: #eaae9b;
                            }
                        `}
                    >
                        <AppBar position="static" css={css``}>
                            <Tabs
                                value={tabValue}
                                onChange={handleTabsChange}
                                aria-label="simple tabs example"
                            >
                                <Tab label="" />
                                {/* <Tab label="Q & A" /> */}
                            </Tabs>
                        </AppBar>
                        {tabValue === 0 ? (
                            <div
                                index={0}
                                css={css`
                                    padding: 16px;
                                    background-color: rgb(55, 58, 71);
                                    color: rgb(209, 183, 176);
                                `}
                            >
                                <Typography variant="body2">
                                    
                                </Typography>
                                <br />
                                <Typography variant="body2">
                                    
                                </Typography>
                                <br />
                                <Typography variant="body2">
                                    
                                    
                                </Typography>
                                <br />
                                <Typography variant="body2">
                                    
                                    
                                </Typography>
                            </div>
                        ) : (
                            <div
                                index={1}
                                css={css`
                                    padding: 16px;
                                    background-color: rgb(55, 58, 71);
                                    color: rgb(209, 183, 176);
                                `}
                            >
                                <Typography variant="subtitle1">
                                    1. ?
                                </Typography>
                                <Typography variant="body2">
                                    
                                </Typography>
                                <br />
                                <Typography variant="subtitle1">
                                    2. ?
                                </Typography>
                                <Typography variant="body2">
                                    0~40
                                </Typography>
                                <br />
                                <Typography variant="subtitle1">
                                    3. ?
                                </Typography>
                                <Typography variant="body2">
                                    
                                </Typography>
                                <br />
                                <Typography variant="subtitle1">
                                    4. ?
                                </Typography>
                                <Typography variant="body2">
                                    APP!
                                </Typography>
                            </div>
                        )}
                    </div>
                </Dialog>
                <div css={[maxWidthContainerStyle, isEnter && showingStyle]}>
                    <Typography
                        css={[
                            fontColorStyle,
                            css`
                                @media (max-width: 420px) {
                                    &.MuiTypography-h4 {
                                        font-size: 26px;
                                    }
                                }
                            `
                        ]}
                        variant="h4"
                    >
                        
                        <br />
                        
                    </Typography>
                    <div>
                        <img
                            css={css`
                                width: 100%;
                                @media (max-width: 768px) {
                                    display: none;
                                }
                            `}
                            src={images.stepsDescriptionPc}
                            alt="chane "
                        />
                        <img
                            css={css`
                                width: 100%;
                                display: none;
                                @media (max-width: 768px) {
                                    display: block;
                                }
                            `}
                            src={images.stepsDescriptionMobile}
                            alt="chane "
                        />
                    </div>
                    <div
                        css={css`
                            position: relative;
                            width: 100%;
                            max-width: 150px;
                            background-color: rgb(236, 236, 237);
                            padding: 24px 0;
                            box-shadow: 0 3px 6px 0 rgba(0, 0, 0, 0.3);
                            border-radius: 11px;
                            margin-bottom: 32px;
                            margin: auto auto 32px;
                            cursor: pointer;
                        `}
                        onClick={handleClickOpen}
                    >
                        
                        <img
                            css={css`
                                width: 100%;
                                max-width: 50px;
                                position: absolute;
                                bottom: -15px;
                                right: -25px;
                                animation: ${bounce} 1s ease infinite;
                            `}
                            src={images.finger}
                            alt="chane"
                        />
                        <div css={iconWarnComStyle}>
                            <span css={iconWarns}>
                                <i css={dotStyle}></i>
                                <i css={pulesStyle}></i>
                            </span>
                        </div>
                    </div>
                    <Typography css={fontColorStyle} variant="subtitle1">
                        
                    </Typography>
                </div>
            </section>
        </ScrollTrigger>
    );
}

export default React.memo(Market);

const rootContainerStyle = css`
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background-image: linear-gradient(-119deg, #eaae9b, #b4c3d0);
    text-align: center;

    @media (max-width: 768px) {
        height: 100%;
        padding: 24px;
    }
`;

const maxWidthContainerStyle = css`
    width: 100%;
    max-width: 1080px;
    opacity: 0;
    transition: 0.8s;
    transform: translateX(10px);
`;

const showingStyle = css`
    opacity: 1;
    transform: translateX(0px);
`;

const fontColorStyle = css`
    color: #2f2e41;
`;

const bounce = keyframes`
  from, 20%, 53%, 80%, to {
    transform: translate3d(0,0,0);
  }

  40%, 43% {
    transform: translate3d(0, -30px, 0);
  }

  70% {
    transform: translate3d(0, -15px, 0);
  }

  90% {
    transform: translate3d(0,-4px,0);
  }
`;

const warnAnimationFrame = keyframes`
0% {
    transform: scale(0);
    opacity: 0.0;
}
25% {
    transform: scale(0);
    opacity: 0.1;
}
50% {
    transform: scale(0.1);
    opacity: 0.3;
}
75% {
    transform: scale(0.5);
    opacity: 0.5;
}
100% {
    transform: scale(1);
    opacity: 0.0;
}
`;

const dotStyle = css`
    position: absolute;
    width: 50px;
    height: 50px;
    left: -15px;
    top: -15px;
    -webkit-border-radius: 100;
    -moz-border-radius: 100;
    border: 20px solid #f6c100;
    border-radius: 100%;
    z-index: 2;
    opacity: 0;
    -webkit-animation: ${warnAnimationFrame} 3s ease-out;
    -moz-animation: ${warnAnimationFrame} 3s ease-out;
    animation: ${warnAnimationFrame} 3s ease-out;
    -webkit-animation-iteration-count: infinite;
    -moz-animation-iteration-count: infinite;
    animation-iteration-count: infinite;
`;

const pulesStyle = css`
    position: absolute;
    width: 50px;
    height: 50px;
    left: -15px;
    top: -15px;
    border: 20px solid #f6c100;
    -webkit-border-radius: 100%;
    -moz-border-radius: 100%;
    border-radius: 100%;
    z-index: 1;
    opacity: 1;
    -webkit-animation: ${warnAnimationFrame} 2s ease-out;
    -moz-animation: ${warnAnimationFrame} 2s ease-out;
    animation: ${warnAnimationFrame} 2s ease-out;
    -webkit-animation-iteration-count: infinite;
    -moz-animation-iteration-count: infinite;
    animation-iteration-count: infinite;
`;

const iconWarnComStyle = css`
    position: absolute;
    top: 8%;
    left: 66%;
`;

const iconWarns = css`
    position: relative;
    width: 100%;
    height: 100%;
    display: block;
`;
